/* CSS для эффекта fade-in */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.form-container {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  margin: 20px 0;
  padding: 1% 10%; /* Добавлены отступы 10% от краев */
  background-color: #F4F4F4;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  height: auto;
  max-width: 100%;
  margin: 0 auto;
  position: relative;
  color: #2c3e50;
}

.info-block, .form-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.6rem;
  text-align: center;
  padding-left: 10%;
  padding-right: 10%;
  background-color: transparent;
}

.info-block {
  justify-content: flex-start;
}

.info-text h1 {
  padding-top: 20%;
  font-size: 2.2rem;
  font-weight: 400;
  margin-bottom: 10px;
  text-align: center;
}

.info-block img {
  width: 80%;
  height: auto;
  transform: scale(1);
  border-radius: 15px;
  object-fit: contain;
  margin-top: auto;
  margin-bottom: 10px;
  padding-bottom: 50%;
}

.form-wrapper {
  position: relative;
  width: 100%;
}

form {
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  transition: opacity 0.5s;
  color: inherit;
}

form.hidden {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

form .form-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 90%;
  margin-bottom: 10px;
}

form label {
  font-size: 1.6rem;
  color: inherit;
  margin-bottom: 5px;
}

form input {
  padding: 10px;
  border: 2px solid #dee2e6;
  border-radius: 10px;
  font-size: 1.6rem;
  width: 100%;
  transition: border-color 0.3s ease;
  color: inherit;
}

form button {
  padding: 10px;
  border: none;
  border-radius: 10px;
  background-color: #41930b;
  color: white;
  cursor: pointer;
  font-size: 1.6rem;
  transition: background-color 0.3s, box-shadow 0.3s, transform 0.3s;
  position: relative;
  overflow: hidden;
  font-weight: bold;
  letter-spacing: 1px;
  width: 35%;
  margin-top: auto;
  margin-bottom: 0px;
}

.error-message {
  color: red;
  font-size: 1.2rem;
  margin-top: 5px;
}

form button:hover {
  background-color: #57d100;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  transform: scale(1.04);
}

.thank-you-slide {
  text-align: center;
  padding: 20px;
  border: 2px solid #dee2e6;
  border-radius: 15px;
  background-color: #ffffff;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  max-width: 1000px;
  margin: 20px auto;
  opacity: 0;
  animation: fadeIn 1s forwards;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: none;
  color: inherit;
}

.thank-you-slide.visible {
  display: block;
}

.thank-you-slide h2 {
  font-size: 2.2rem;
  color: inherit;
  margin-bottom: 20px;
}

.thank-you-slide p {
  font-size: 1.6rem;
  color: inherit;
  margin-bottom: 10px;
}

.consent-text {
  font-size: 1rem;
  color: inherit;
  margin-bottom: 10px;
}

/* Адаптивные стили */
@media (max-width: 1024px) {
  .form-container {
    flex-direction: column;
    padding: 20px 10%;
  }

  .info-block, .form-wrapper {
    margin: 10px 0;
  }

  .info-text h1 {
    font-size: 1.8rem;
  }

  form label {
    font-size: 1.4rem;
  }

  form input {
    font-size: 1.4rem;
  }

  form button {
    font-size: 1.4rem;
  }
}

@media (max-width: 768px) {
  .form-container {
    padding: 15px 10%;
  }

  .info-text h1 {
    font-size: 1.6rem;
  }

  form label {
    font-size: 1.2rem;
  }

  form input {
    font-size: 1.2rem;
  }

  form button {
    font-size: 1.2rem;
  }

  .thank-you-slide h2 {
    font-size: 1.8rem;
  }

  .thank-you-slide p {
    font-size: 1.2rem;
  }
}

@media (max-width: 480px) {
  .form-container {
    padding: 10px 10px 10px 10px;
    flex-direction: column;
  }

  .info-block {
    order: 3; /* Перемещаем блок изображения вниз */
  }

  .form-wrapper {
    order: 2; /* Форма остается в центре */
  }

  .info-text {
    order: 1; /* Текстовый блок поднимаем наверх */
  }

  .info-text h1 {
    font-size: 1.4rem;
  }

  form label {
    font-size: 1rem;
  }

  form input {
    font-size: 1rem;
  }

  form button {
    font-size: 1rem;
  }

  .thank-you-slide h2 {
    font-size: 1.6rem;
  }

  .thank-you-slide p {
    font-size: 1rem;
  }
}