/* Основные стили для секции портфолио */
.portfolio {
  text-align: center;
  background-color: #ffffff;
  border-bottom: 2px solid #ddd;
  border-top: 2px solid #ddd;
  padding-bottom: 40px;
}

.portfolio h2 {
  font-size: 46px;
  margin: 50px auto;
  background: #2c3e50;
  color: white;
  padding: 20px 150px;
  width: fit-content;
  border-radius: 10px;
}

/* Стили для слайдера портфолио */
.portfolio-slider {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 0 10%; /* Отступы слева и справа */
  border: 2px solid #2c3e50; /* Толстая граница */
  border-radius: 10px; /* Закругленные углы */
}

.portfolio-slide {
  width: 100%;
  text-align: center;
  position: relative;
  transition: opacity 0.5s ease-in-out;
}

.portfolio-slide img {
  height: 80vh;
  width: 100%;
  object-fit: cover;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px 10px 0 0; /* Закругляем только верхние углы */
}

/* Стили для текста на подложке */
.portfolio-slide .text-block {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30%;
  background-color: rgba(44, 62, 80, 0.9);
  padding: 20px;
  box-sizing: border-box;
  text-align: left;
  color: white;
  border-radius: 0 0 10px 10px; /* Закругляем только нижние углы */
  overflow: hidden; /* Предотвращаем выход контента за границы слайдера */
}

.portfolio-slide .text-block p {
  margin: 5px 0;
  font-size: 36px;
  font-weight: normal;
  font-family: 'Open Sans', sans-serif;
}

.portfolio-slide .text-block span {
  font-weight: bolder;
}

/* Стили для кнопок управления слайдером */
.portfolio-prev, .portfolio-next {
  background: rgba(44, 62, 80, 0.7);
  border-radius: 50%;
  border: none;
  cursor: pointer;
  padding: 20px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.3s;
}

.portfolio-prev img, .portfolio-next img {
  width: 64px;
  height: 64px;
}

.portfolio-prev:hover, .portfolio-next:hover {
  transform: scale(1.1) translateY(-50%);
}

.portfolio-prev {
  left: 10px;
}

.portfolio-next {
  right: 10px;
}

/* Анимации для смены слайдов */
.fade-out {
  opacity: 0;
  transition: opacity 0.75s ease-in-out;
}

.fade-in {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

/* Адаптивность для различных экранов */
@media (max-width: 1024px) {
  .portfolio h2 {
    font-size: 40px;
  }

  .portfolio-slide .text-block p {
    font-size: 28px; /* Уменьшаем размер шрифта */
  }

  .portfolio-prev img, .portfolio-next img {
    width: 48px;
    height: 48px;
  }
}

@media (max-width: 768px) {
  .portfolio h2 {
    font-size: 32px;
    padding: 8px;
  }

  .portfolio-slide img {
    height: 60vh;
  }

  .portfolio-slide .text-block p {
    font-size: 24px; /* Уменьшаем размер шрифта */
  }

  .portfolio-prev img, .portfolio-next img {
    width: 32px;
    height: 32px;
  }

  .portfolio-prev, .portfolio-next {
    padding: 10px;
  }

  .portfolio-slide .text-block {
    position: static;
    background-color: transparent;
    padding: 10px 0;
    margin-top: 10px;
  }
}

@media (max-width: 480px) {
  .portfolio h2 {
    font-size: 28px;
    padding: 6px;
  }

  .portfolio-slide img {
    height: 450px;
  }

  .portfolio-slide .text-block {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 30%;
    background-color: rgba(44, 62, 80, 0.9);
    padding: 10px;
    box-sizing: border-box;
    text-align: left;
    color: white;
    overflow: hidden;
  }

  .portfolio-slide .text-block p {
    font-size: 20px; /* Уменьшаем размер шрифта */
  }

  .portfolio-prev img, .portfolio-next img {
    width: 24px;
    height: 24px;
  }

  .portfolio-prev, .portfolio-next {
    padding: 5px;
  }
}